@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
  color: #000000;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

label {
  color: #000000;
}

body {
  height: 100vh;
}

.izquierda {
  display: flex;
  justify-content: flex-end;
}
@media (min-width: 1024px) {
  .izquierda {
    margin-top: 20px;
  }
}

.dark-mode-boton {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 2rem;
  margin-top: 0;
  margin-bottom: 0;
  animation-duration: 1s;
  animation-name: slidein;
}
.dark-mode-boton:hover {
  cursor: pointer;
}

@keyframes slidein {
  from {
    width: 0.5rem;
  }
  to {
    width: 3rem;
  }
}
.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode table {
  background-color: #333333;
}
.dark-mode td {
  color: #FFFFFF;
}
.dark-mode #buscador {
  background-color: #333333;
}
.dark-mode #paginacion-agenda div img {
  filter: invert(100%);
}
.dark-mode .administracion #no-more-tables tr {
  border: 2px solid #fff;
}
.dark-mode th {
  color: #FFFFFF;
}
.dark-mode td {
  border-bottom: #FFFFFF;
}
.dark-mode header {
  background-color: #333333;
  border-bottom: 2px solid #FFFFFF;
}
.dark-mode .dark-mode-boton {
  filter: invert(100%);
}
.dark-mode .menu {
  filter: invert(100%);
}
.dark-mode #up {
  filter: invert(100%);
}
.dark-mode .informacion a {
  color: #FFFFFF;
}
.dark-mode .informacion span {
  color: #FFC107;
  font-weight: 900;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #FFFFFF;
}
.dark-mode footer {
  background-color: #333333;
  border-top: 2px solid #FFFFFF;
}
.dark-mode footer p {
  color: #FFFFFF;
}
.dark-mode .menu-login {
  border-color: #FFFFFF;
}
.dark-mode .menu-login h2 {
  color: #FFFFFF;
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: #FFFFFF;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  border: 2px solid #FFFFFF;
  color: #FFFFFF;
}
.dark-mode .video .overlay {
  background: #353535;
  background: linear-gradient(270deg, rgba(39, 39, 39, 0.75) 0%, rgba(0, 0, 0, 0.5) 100%);
}
.dark-mode .boton-login {
  background-color: #FFFFFF;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  border-radius: 3rem;
  color: #000000;
}
.dark-mode .boton-login:hover {
  background-color: #f2f2f2;
  cursor: pointer;
}
.dark-mode .boton-login:hover {
  background-color: #000000;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: block;
  border: none;
  color: #FFFFFF;
  box-shadow: 1px 1px 3px #000000;
}
.dark-mode .boton-login:hover:hover {
  background-color: black;
  cursor: pointer;
}

