.administracion {
  height: auto;
}
.administracion h2 {
  margin-top: 5rem;
}
.administracion h3 {
  text-align: center;
  margin-bottom: 5rem;
}
.administracion .herramientas-Usuarios {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.administracion .herramientas-Usuarios .input-group {
  width: 60%;
}
.administracion .herramientas-Usuarios .input-group input {
  font-size: 1.6rem;
}
@media (min-width: 768px) {
  .administracion .herramientas-Usuarios {
    flex-direction: row;
    justify-content: space-between;
    align-items: baseline;
  }
  .administracion .herramientas-Usuarios .input-group {
    width: 33%;
  }
}
.administracion .herramientas-Usuarios div {
  margin-bottom: 1.25rem;
}
.administracion .herramientas-Usuarios div .input-group {
  width: 90%;
}
.administracion #no-more-tables {
  margin-bottom: 5rem;
}
@media only screen and (max-width: 768px) {
  .administracion {
    /* Force table to not be like tables anymore */
    /* Hide table headers (but not display: none;, for accessibility) */
    /*
            Label the data
            */
  }
  .administracion #no-more-tables table,
.administracion #no-more-tables thead,
.administracion #no-more-tables tbody,
.administracion #no-more-tables th,
.administracion #no-more-tables td,
.administracion #no-more-tables tr {
    display: block;
  }
  .administracion #no-more-tables thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }
  .administracion #no-more-tables tr {
    border: 1px solid #000000;
    border-spacing: 0px 20px;
    vertical-align: middle;
  }
  .administracion #no-more-tables td {
    /* Behave  like a "row" */
    border: none;
    border-bottom: 1px solid #a1a1a1;
    position: relative;
    padding-left: 40%;
    white-space: normal;
    text-align: left;
  }
}
@media only screen and (max-width: 768px) and (min-width: 480px) {
  .administracion #no-more-tables td {
    padding-left: 50%;
  }
}
@media only screen and (max-width: 768px) {
  .administracion #no-more-tables td:before {
    /* Now like a table header */
    position: absolute;
    /* Top/left values mimic padding */
    top: 6px;
    left: 6px;
    width: 45%;
    padding-right: 10px;
    white-space: nowrap;
    text-align: left;
    font-weight: bold;
  }
}
@media only screen and (max-width: 768px) {
  .administracion #no-more-tables td:before {
    content: attr(data-title);
  }
}
.administracion #paginacion-agenda {
  display: flex;
  justify-content: space-around;
  margin-bottom: 1rem;
}
.administracion #paginacion-agenda div {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 0.5rem;
  width: 5rem;
  height: 5rem;
  border: 2px solid #000000;
  cursor: pointer;
  box-shadow: 2px 2px 10px #1b1b1b;
}
.administracion #paginacion-agenda div img {
  width: 3rem;
}
.administracion #paginacion-agenda div a.disabled {
  pointer-events: none;
  cursor: default;
}
.administracion #paginacion-agenda :hover {
  box-shadow: none;
}
.administracion #paginacion-agenda :hover img {
  width: 4rem;
}
.administracion #paginacion-agenda .disabled {
  background-color: #FFFFFF;
  box-shadow: none;
  cursor: default;
}
.administracion #paginacion-agenda .disabled img {
  width: 3rem;
}
.administracion #paginacion-agenda .disabled :hover img {
  width: 3rem;
}

.dark-mode #paginacion-agenda div {
  border: 2px solid #FFFFFF;
  background-color: #000000;
  box-shadow: 2px 2px 10px #626262;
}
.dark-mode #paginacion-agenda .disabled {
  background-color: #333333;
  cursor: default;
}

