header {
  background-color: #FFFFFF;
  border-bottom: 2px solid #000000;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  box-shadow: 2px 2px 10px #666;
}
@media (min-width: 768px) {
  header {
    padding: 2.5rem 0.5rem 2.5rem 0.5rem;
  }
}
header .menu {
  width: 4rem;
}
@media (min-width: 768px) {
  header {
    flex-direction: row;
  }
}

h1 {
  margin: 10px 0 0 0;
  font-weight: 900;
  color: #000000;
  text-align: center;
}
@media (min-width: 768px) {
  h1 {
    margin: 0 0 0 2.5rem;
    font-size: 4rem;
  }
}

.informacion {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.informacion p {
  margin: 0;
  margin-bottom: 15px;
  font-size: 1.6rem;
}
.informacion a {
  margin-bottom: 15px;
  text-decoration: none;
  font-size: 1.4rem;
  color: #bd0707;
  font-weight: 900;
  letter-spacing: 0.2rem;
  text-transform: uppercase;
}
.informacion .menu:hover {
  cursor: pointer;
  box-shadow: 2px 2px 10px #666;
  border-radius: 1rem;
  padding: 1.5px;
}
.informacion img {
  margin-bottom: 15px;
}
.informacion span {
  font-size: 1.6rem;
  font-weight: 900;
  letter-spacing: 0.2rem;
  text-transform: capitalize;
}
@media (min-width: 768px) {
  .informacion {
    flex-direction: row;
    align-items: center;
  }
  .informacion p, .informacion a, .informacion img {
    margin-bottom: 0;
    margin-right: 2.5rem;
  }
}

