.modal {
  display: none;
  /* Hidden by default */
  position: fixed;
  /* Stay in place */
  z-index: 1;
  /* Sit on top */
  left: 0;
  top: 0;
  width: 100%;
  /* Full width */
  height: 100%;
  /* Full height */
  overflow: auto;
  /* Enable scroll if needed */
  background-color: black;
  /* Fallback color */
  background-color: rgba(0, 0, 0, 0.4);
  /* Black w/ opacity */
  -webkit-animation-name: fadeIn;
  /* Fade in the background */
  -webkit-animation-duration: 0.4s;
  animation-name: fadeIn;
  animation-duration: 0.4s;
}
.modal .modal-content {
  position: fixed;
  width: 66%;
  height: 66%;
  position: absolute;
  top: 0%;
  left: 0%;
  right: 0%;
  bottom: 0%;
  transform: translate(0%, 0%);
  margin-top: auto;
  margin-bottom: auto;
  margin-left: auto;
  margin-right: auto;
  background-color: #FFFFFF;
  border: 2px solid #000000;
  border-radius: 2.5rem;
}
.modal .modal-content nav {
  margin-top: 50px;
  padding-bottom: 0px;
}
.modal .modal-content ul,
.modal .modal-content li {
  margin: 0;
  padding: 0;
  list-style: none;
  color: black;
  margin: 20px;
}
.modal .modal-content ul a,
.modal .modal-content li a {
  display: block;
  text-align: center;
  color: #000000;
  border: 2px solid #000000;
  background-color: #FFFFFF;
  box-shadow: 2px 2px 10px #666;
  border-radius: 2.5rem;
  padding: 1rem;
  text-transform: uppercase;
}
.modal .modal-content ul :hover,
.modal .modal-content li :hover {
  font-weight: 900;
  box-shadow: none;
}
.modal .modal-content div {
  margin: 0 auto;
}

