@use 'base/variables' as v;
.formulario {
    p {
        font-size: 1.4rem;
        color: v.$grisOscuro;
        margin: 2rem 0 0 0;
    }

    legend {
        font-size: 2rem;
        color: v.$grisOscuro;
    }

    label, input {
        font-weight: v.$bold;
        text-transform: uppercase;
        display: block;
        text-align: center;
    }
    input:not([type="submit"]), 
    textarea,
    select {
        padding: 1rem;
        display: block;
        width: 100%;
        background-color: #f9f9f9f9;
        border: 2px solid v.$negro;
        border-radius: 1rem;
        margin-bottom: 2rem;
        font-weight: v.$regular;
        text-transform: none;
    }

    textarea {
        height: 20rem;
    }

    .forma-contacto {
        max-width: 30rem;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    input[type="radio"] {
        margin: 0;
        width: auto;
    }
}

